<template>
  <page
    :grid-config="pageConfig"
  >
    <template v-slot:header>
      <!-- <page-auth-header /> -->
    </template>
    <template v-slot:body>
      <div :class="$style.body">
        <!-- <span :class="$style['body-title']">
          Необходимо
          <br />
          войти
        </span> -->
        <!-- <div :class="$style['input-block']"> -->
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <el-avatar :size="size" :src="circleUrl"></el-avatar>
            </div>
              <el-input
                placeholder="Логин"
                v-model="login"
                label="Пароль"
              />
              <el-input
                placeholder="Пароль"
                v-model="password"
                show-password
                label="Пароль"
              />
          </el-card>
          <el-button
            type="primary"
            :loading="requestInProgress"
            @click="authProcedureStart"
            :class="$style.test"
          >Продолжить</el-button>
      </div>
    </template>
  </page>
</template>

<script>
import { mapActions } from 'vuex';
import { mapFields } from 'vuex-map-fields';
import { required } from 'vuelidate/lib/validators';
// import { }
// import { BaseInput } from '@/components/inputs';
// import Checkbox from '@/components/Checkbox';
// import BaseButton from '@/components/BaseButton';
import Page from '@/components/Page';
// import PageFooter from '@/components/PageFooter';

export default {
  name: 'AuthPage',
  components: {
    // BaseButton,
    // BaseInput,
    // Checkbox,
    Page,
    // PageFooter,
  },
  data() {
    return {
      pageConfig: {
        rowsCount: 2,
        flexibleRow: 2,
      },
    };
  },
  computed: {
    ...mapFields({
      login: 'auth.login',
      password: 'auth.password',
      consent: 'auth.consent',
      error: 'error.statusType',
      requestInProgress: 'requestInProgress',
    }),
    isPageValid() {
      return this.$v.login.required
        && this.$v.password.required
        && this.consent;
    },
    bodyStyle() {
      return {
        height: `${window.screen.availHeight - 185}px`,
      };
    },
  },
  methods: {
    ...mapActions([
      'authProcedure',
      'removeAuthInfo',
      'testFetch',
    ]),
    handleChangeConsent(value) {
      this.$store.commit('SET_AUTH_CONSENT', value);
    },
    async authProcedureStart() {
      // await this.testFetch();
      await this.authProcedure();
      if (!this.error) {
        this.$router.push('/main');
      }
    },
  },
  mounted() {
    this.removeAuthInfo();
  },
  validations: {
    login: {
      required,
    },
    password: {
      required,
    },
    consent: {
      required,
    },
  },
};
</script>

